import React, { useState, useEffect } from "react";
import TopHeader from "../../components/TopHeader/TopHeader";
import SidebarNav from "../../components/SideBar/SideBar";
import SidebarMenuItems from "../../utils/SidebarMenuItems";
import "./DelegationAlllTask.scss";
import MyTasks from "../../components/MyTasks/MyTasks";
import ManageTeamTasks from "../../components/ManageTeamTasks/ManageTeamTasks";
import ManageAllTeamTasks from "../../components/ManageAllTeamTasks/ManageAllTeamTasks";

function TeamTask() {
  const storedAccessList = localStorage.getItem("accessList");
  const parsedAccessList = JSON.parse(storedAccessList);

  console.log(parsedAccessList);

  const [taskInfo, setTaskInfo] = useState({
    title: "",
    assignTo: "",
    completionDate: "",
  });

  const [activeItemId, setActiveItemId] = useState(2); // Default active item id
  const [stateAccessList, setStateAccessList] = useState(
    parsedAccessList || {}
  );

  // parsedAccessList.Delegation = {
  //   create: false,
  //   view: false,
  //   update: true,
  //   delete: true,
  //   all: false
  // };

  useEffect(() => {
    setStateAccessList(parsedAccessList);
  }, [parsedAccessList]);

  // console.log("setStateAccessList", stateAccessList.Delegation.create);

  const handleInputChange = (e) => {
    const { id, value } = e.target;
    setTaskInfo((prevTaskInfo) => ({
      ...prevTaskInfo,
      [id]: value,
    }));
  };

  const handleSaveButtonClick = () => {
    // Add logic for saving the task
  };

  const handleBodyNavigationClick = (itemId) => {
    setActiveItemId(itemId);
  };

  const bodyNavigationItems = [
    { id: 1, name: "My Tasks" },
    { id: 2, name: "Manage Team Tasks" },
    { id: 3, name: "Manage All Team Tasks" },
  ];

  return (
    <div className="teamTask">
      <TopHeader />
      <div className="dashboardBody">
        <SidebarNav menuItems={SidebarMenuItems} />
        <div className="dashboardContent">
          <div className="title">Tasks</div>
          <div className="searchContainer">
            <div className="formFields">
              <div className="formInput">
                <label htmlFor="title">Search </label>
                <div className="inputField">
                  <input
                    type="text"
                    id="title"
                    onChange={handleInputChange}
                    value={taskInfo.title}
                    placeholder="Enter title"
                  />
                </div>
              </div>
              <div className="formInput">
                <label htmlFor="assignTo">Status </label>
                <div className="inputField">
                  <select
                    id="assignTo"
                    onChange={handleInputChange}
                    value={taskInfo.assignTo}
                  >
                    <option value="" disabled>
                      Select assignee
                    </option>
                    <option value="assignee1">Assignee 1</option>
                    <option value="assignee2">Assignee 2</option>
                  </select>
                </div>
              </div>
              <div className="formInput">
                <label htmlFor="completionDate">Select Week </label>
                <div className="inputField">
                  <input
                    type="week"
                    id="completionDate"
                    onChange={handleInputChange}
                    value={taskInfo.completionDate}
                    placeholder="Enter completion date"
                  />
                </div>
              </div>
              <div className="formInput">
                <label htmlFor="completionDate">Date </label>
                <div className="inputField">
                  <input
                    type="date"
                    id="completionDate"
                    onChange={handleInputChange}
                    value={taskInfo.completionDate}
                    placeholder="Enter completion date"
                  />
                </div>
              </div>
              <div className="searchBtns">
                <button onClick={handleSaveButtonClick} className="proceedBtn">
                  Apply
                </button>
                <button
                  onClick={handleSaveButtonClick}
                  className="proceedBtn clearBtn"
                >
                  Clear All
                </button>
              </div>
            </div>
          </div>
          <div className="teamTaskContainer">
            <div className="bodyNavigation">
              {bodyNavigationItems.map((item) =>
                (item.id !== 2 && item.id !== 3) ||
                stateAccessList.delegation.all ||
                (stateAccessList.delegation.update && item.id !== 2) ||
                (stateAccessList.delegation.create && item.id !== 3) ||
                (stateAccessList.delegation.create &&
                  stateAccessList.delegation.view &&
                  item.id === 3) ? (
                  <p
                    key={item.id}
                    className={`title ${
                      activeItemId === item.id ? "active" : ""
                    }`}
                    onClick={() => handleBodyNavigationClick(item.id)}
                  >
                    {item.name}
                  </p>
                ) : null
              )}
            </div>

            {/* <div className="bodyNavigation">
              {bodyNavigationItems.map((item) => (
                item.id === 1 || (
                  (item.id === 2 && stateAccessList.delegation.create && !stateAccessList.delegation.view) ||
                  (item.id === 3 && stateAccessList.delegation.view && stateAccessList.delegation.create && stateAccessList.delegation.update)
                ) ? (
                  <p
                    key={item.id}
                    className={`title ${activeItemId === item.id ? 'active' : ''}`}
                    onClick={() => handleBodyNavigationClick(item.id)}
                  >
                    {item.name}
                  </p>
                ) : null
              ))}
            </div> */}

            {activeItemId === 1 && <MyTasks />}
            {activeItemId === 2 && <ManageTeamTasks />}
            {activeItemId === 3 && <ManageAllTeamTasks />}
          </div>
        </div>
      </div>
    </div>
  );
}

export default TeamTask;
