import React, { useState, useEffect } from "react";
import TopHeader from "../../components/TopHeader/TopHeader";
import SidebarNav from "../../components/SideBar/SideBar";
import SidebarMenuItems from "../../utils/SidebarMenuItems";
import "./Permissions.scss";
import { Link } from "react-router-dom";
import Delegation from "../../components/Delegation/Delegation";
import Checklist from "../../components/Checklist/Checklist";
import EmployeeLeave from "../../components/UserManagement/UserManagement";
import Cookies from "js-cookie";
import getPermissionList from "../../interface/implementation/getPermissionListImp";
import { showToasterMessage } from "../../utils/helper";

function Permissions() {
  const handleInputChange = () => {
    console.log("create");
  };

  const showToast = (errorText) => {
    showToasterMessage(errorText);
  };

  const [activeItemId, setActiveItemId] = useState(1);

  const handleBodyNavigationClick = (itemId) => {
    setActiveItemId(itemId);
  };

  const bodyNavigationItems = [
    { id: 1, name: "Delegation" },
    { id: 2, name: "Checklist" },
    { id: 3, name: "User Management" },
  ];

  const [permissionInfo, setPermissionInfo] = useState([
    {
      pm_id: "",
      system_name: "",
      create: "",
      update: "",
      view: "",
      delete: "",
      all: "",
    },
  ]);

  useEffect(() => {
    const getAllPermissionList = () => {
      const token = Cookies.get("token");
      getPermissionList(token)
        .then((res) => {
          const mappedPermissions = res.result.map((permissions) => ({
            pm_id: permissions.pm_id || "",
            system_name: permissions.system_name || "",
            create: permissions.create || "",
            update: permissions.update || "",
            view: permissions.view || "",
            delete: permissions.delete || "",
            all: permissions.all || "",
          }));
          setPermissionInfo(mappedPermissions);
          console.log(permissionInfo);
          showToast(res);
        })
        .catch((error) => {
          console.log("error during getting deptList:", error);
          showToast("An error occurred during fetching All department List");
        });
    };
    getAllPermissionList();
  }, []);

  return (
    <div className="permissions">
      <TopHeader />
      <div className="dashboardBody">
        <SidebarNav menuItems={SidebarMenuItems} />
        <div className="dashboardContent">
          <div className="headerBody">
            <h2>Permissions list</h2>
            <div className="actionHeader">
              <div className="formInput">
                <div className="inputField">
                  <input
                    type="text"
                    id="completionDate"
                    onChange={handleInputChange}
                    value={""}
                    placeholder="Search..."
                  />
                </div>
              </div>
              {/* <Link to={'/holiday/create'} className="tableBtn">+ Add New Holiday List</Link> */}
            </div>
          </div>
          <div className="permissionsContainer">
            <div className="bodyNavigation">
              {bodyNavigationItems.map((item) => (
                <p
                  key={item.id}
                  className={`title ${
                    activeItemId === item.id ? "active" : ""
                  }`}
                  onClick={() => handleBodyNavigationClick(item.id)}
                >
                  {item.name}
                </p>
              ))}
            </div>
            {activeItemId === 1 && <Delegation />}
            {activeItemId === 2 && <Checklist />}
            {activeItemId === 3 && <EmployeeLeave />}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Permissions;
