import React, { useState } from 'react';
import TopHeader from '../../components/TopHeader/TopHeader';
import SidebarNav from '../../components/SideBar/SideBar';
import SidebarMenuItems from '../../utils/SidebarMenuItems';
import autoCompleteName from "../../interface/implementation/autoCompleteNameAPIImp";
import './DelegationCreateTask.scss';
import Cookies from "js-cookie";

function DelegationCreateTask() {
  const [empNames, setEmpNames] = useState([]);
  const [taskInfo, setTaskInfo] = useState({
    title: '',
    assignTo: '',
    completionDate: '',
  });
  const token = Cookies.get("token");
  // console.log(token);

  const handleInputChange = (e) => {
    const { id, value } = e.target;
    setTaskInfo((prevTaskInfo) => ({
      ...prevTaskInfo,
      [id]: value,
    }));
    let body = {
      empName: e.target.value,
    };
    if (id === "viewName") {
      let body = {
        empName: value,
      };
      autoCompleteName(body, token)
        .then((res) => {
          console.log("emp names", res.result);
          setEmpNames(res.result);
          const firstResult = res.result[0];
          if (firstResult) {
            const userId = firstResult.user_id;
            setTaskInfo((prevTaskInfo) => ({
              ...prevTaskInfo,
              assignTo: userId, // Set assignTo to user_id
            }));
          }
        })
        .catch((err) => console.log("err", err));
    }
  };

  const handleAssignToChange = (e,selectedValue) => {
    e.stopPropagation();
    console.log('Selected item:', e.emp_name);
    setTaskInfo((prevTaskInfo) => ({
      ...prevTaskInfo,
      assignTo: selectedValue,
    }));
    setEmpNames([]);
  };

  const handleSaveButtonClick = () => {
    if (!taskInfo.title || !taskInfo.assignTo || !taskInfo.completionDate) {
      alert('Please fill in all required fields');
      return;
    }
    const taskData = {
      task_name: taskInfo.title,
      assigned_to: taskInfo.assignTo,
      assigned_date: taskInfo.completionDate,
    };
    saveTask(taskData);
  };

  const saveTask = (taskData) => {
    fetch('http://3.111.244.86:8443/api/createDelegation', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify(taskData),
    })
      .then((response) => {
        if (!response.ok) {
          throw new Error('Failed to save task');
        }
        return response.json();
      })
      .then((data) => {
        console.log('Task saved successfully:', data);
        // Optionally, perform any necessary actions after saving the task
      })
      .catch((error) => {
        console.error('Error saving task:', error);
        
      });
  };

  return (
    <div className="createTask">
      <TopHeader />
      <div className="dashboardBody">
        <SidebarNav menuItems={SidebarMenuItems} />
        <div className="dashboardContent">
          <div className="title">Create Task</div>
          <div className="allTaskContainer">
            <p className="taskTitle">New Task</p>
            <div className="formFields">
              <div className='formInput'>
                <label htmlFor="title">Title </label>
                <div className="inputField">
                  <input
                    type="text"
                    id="title"
                    onChange={handleInputChange}
                    value={taskInfo.title}
                    placeholder='Enter title'
                  />
                </div>
              </div>
              <div className='formInput'>
                <label htmlFor="assignTo">Assign To</label>
                <div className="inputField searchField ">
                  <input
                    type="text"
                    id="viewName"
                    onChange={(e) => handleInputChange(e)}
                    placeholder="Search and select"
                  />
                  {empNames?.length > 0 && (
                    <div className="searchDropdown">
                      {empNames?.map((item, index) => (
                        <div key={index} onClick={(e) => handleAssignToChange(e,item.emp_name)}>
                          <p>{item.emp_name}</p>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              </div>
              <div className='formInput'>
                <label htmlFor="completionDate">Completion Date </label>
                <div className="inputField">
                  <input
                    type="date"
                    id="completionDate"
                    onChange={handleInputChange}
                    value={taskInfo.completionDate}
                    placeholder='Enter completion date'
                  />
                </div>
              </div>
            </div>
            <div>
              <button onClick={handleSaveButtonClick} className="proceedBtn">Save</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default DelegationCreateTask;
