import React, { useEffect, useState } from "react";
import TopHeader from "../../components/TopHeader/TopHeader";
import SidebarNav from "../../components/SideBar/SideBar";
import SidebarMenuItems from "../../utils/SidebarMenuItems";
import "./CreateEditRoles.scss";
import ImageList from "../../utils/ImageList";
import { Link, useNavigate, useParams, useLocation } from "react-router-dom";
import { showToasterMessage } from "../../utils/helper";
import Cookies from "js-cookie";
import newRole from "../../interface/implementation/newRoleImp";
import editRoleImp from "../../interface/implementation/editRoleImp";

function CreateEditRoles() {
  const { type } = useParams();

  const navigate = useNavigate();

  const [pageType, setPageType] = useState("");

  useEffect(() => {
    setPageType(type);
  }, [type]);

  const showToast = (errorText) => {
    showToasterMessage(errorText);
  };

  const location = useLocation();

  const [editRole, setEditRole] = useState(() => {
    if (type === "edit") {
      return {
        role_id: location.state.editRole.role_id,
        role_name: location.state.editRole.role_name,
      };
    }
    // else {
    //   return {
    //     role_id: "",
    //     role_name: "",
    //   };
    // }
  });

  // new role
  const [role, setRole] = useState([
    {
      role_name: "", //to validate empty field (first)
    },
  ]);

  // to avoid empty post
  const [addRole, setAddRole] = useState([]);

  const handleInputChange = (e, index) => {
    const { id, value } = e.target;
    if (pageType == "edit") {
      setEditRole({
        ...editRole,
        [id]: value,
      });
    } else {
      let data = [...role];
      data[index][e.target.id] = e.target.value;
      setAddRole(data);
    }
  };

  // submit
  const handleNextButtonClick = () => {
    if (pageType === "edit") {
      if (editRole.role_name.length > 0) {
        let body = {
          ...editRole,
        };
        const token = Cookies.get("token");
        editRoleImp(body, token)
          .then((res) => {
            showToast(res);
            navigate("/roles");
          })
          .catch((error) => {
            console.log("error editing role:", error);
            showToast("An error occurred during Role edit");
          });
      } else {
        showToast("fields can't be empty");
      }
    } else {
      if (role[0].role_name.length <= 0) {
        showToast("Please enter atleast one Role");
      } else {
        let body = [
          // ...role
          ...addRole,
        ];

        const token = Cookies.get("token");

        newRole(body, token)
          .then((res) => {
            showToast(res);
            navigate("/roles");
          })
          .catch((error) => {
            console.log("error during newRole post:", error);
            showToast("An error occurred during new Role creation");
          });
      }
    }
  };

  const addFields = () => {
    let object = {
      role_name: "",
    };
    setRole([...role, object]);
  };

  const removeFields = (index) => {
    if (role.length > 1) {
      let data = [...role];
      data.splice(index, 1);
      setRole(data);
      setAddRole(data); // to avoid mid empty field post
    } else {
      showToast("can't delete the only fiels");
    }
  };

  return (
    <div className="createEditRoles">
      <TopHeader />
      <div className="dashboardBody">
        <SidebarNav menuItems={SidebarMenuItems} />
        <div className="dashboardContent">
          <div className="headerBody">
            <h2>{pageType === "edit" ? "Edit" : "Create"} Roles</h2>
            <div className="actionHeader">
              <Link to={"/roles"} className="tableBtn">
                {" "}
                Back
              </Link>
            </div>
          </div>
          <div className="createEditRolesContainer">
            {role.map((form, index) => {
              return (
                <div className="addRolesContent">
                  <div className="formFields">
                    <div className="formInput">
                      <label htmlFor="departmentName">
                        Name of the Role *{" "}
                      </label>
                      <div className="inputField">
                        <input
                          id="role_name"
                          value={
                            pageType === "edit"
                              ? editRole.role_name
                              : role[index].role_name
                          }
                          onChange={(e) => handleInputChange(e, index)}
                          placeholder="Enter department name"
                        />
                      </div>
                    </div>
                  </div>
                  {pageType === "create" && (
                    <div className="formActions">
                      <p className="add" onClick={addFields}>
                        <img src={ImageList.AddBlueIcon} alt="" /> Add new
                        department
                      </p>
                      <p className="delete" onClick={() => removeFields(index)}>
                        <img src={ImageList.DeleteRedIcon} alt="" /> Delete
                      </p>
                    </div>
                  )}
                </div>
              );
            })}
            <button onClick={handleNextButtonClick} className="nextBtn">
              {pageType === "edit" ? "Update" : "Save"}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CreateEditRoles;
