import React, { useEffect, useState, useRef } from "react";
import TopHeader from "../../components/TopHeader/TopHeader";
import SidebarNav from "../../components/SideBar/SideBar";
import SidebarMenuItems from "../../utils/SidebarMenuItems";
import "./CreateEditEmployees.scss";
import { Link } from "react-router-dom";
import { useParams } from "react-router-dom";
import ImageList from "../../utils/ImageList";
import { showToasterMessage } from "../../utils/helper";
import Cookies from "js-cookie";
import { useLocation, useNavigate } from "react-router-dom";
import newEmp from "../../interface/implementation/newEmpImp";
import getDeptList from "../../interface/implementation/getDeptListImp";
import getroleList from "../../interface/implementation/getRoleListImp";
import editEmpImp from "../../interface/implementation/editEmpImp";

function CreateEditEmployees() {
  const { type } = useParams();

  const navigate = useNavigate();

  const [pageType, setPageType] = useState("");

  useEffect(() => {
    setPageType(type);
  }, [type]);

  const showToast = (errorText) => {
    showToasterMessage(errorText);
  };

  // for dropDown selection
  const [deptOptions, setDeptOptions] = useState([
    {
      dept_id: "",
      dept_name: "",
    },
  ]);

  // for dropDown selection
  const [roleOptions, setRoleOptions] = useState([
    {
      role_id: "",
      role_name: "",
    },
  ]);

  useEffect(() => {
    // call depatment api
    const getAllDeptList = () => {
      const token = Cookies.get("token");
      getDeptList(token)
        .then((res) => {
          const mappedDepartments = res.result.map((department) => ({
            dept_id: department.dept_id || "",
            dept_name: department.dept_name || "",
          }));
          setDeptOptions(mappedDepartments);
        })
        .catch((error) => {
          console.log("error during getting deptList:", error);
          showToast("An error occurred during fetching All department List");
        });
    };
    getAllDeptList();

    //call roles api
    const getAllRoleList = () => {
      const token = Cookies.get("token");
      getroleList(token)
        .then((res) => {
          const mappedRoles = res.result.map((role) => ({
            role_name: role.role_name || "", // Use empty string if null
            role_id: role.role_id || "", // Use empty string if null
          }));
          setRoleOptions(mappedRoles);
        })
        .catch((error) => {
          console.log("error during getting roleList:", error);
          showToast("An error occurred during fetching All role List");
        });
    };
    getAllRoleList();
  }, []);

  const location = useLocation();

  // passed from ListPage
  const [editEmp, setEditEmp] = useState(() => {
    if (type === "edit") {
      return {
        user_id: location.state.editEmp.user_id,
        emp_name: location.state.editEmp.emp_name,
        dept_name: location.state.editEmp.dept_name,
        role_name: location.state.editEmp.role_name,
        dept_id: location.state.editEmp.dept_id,
        role_id: location.state.editEmp.role_id,
        emp_email: location.state.editEmp.emp_email,
        phone_no: location.state.editEmp.phone_no,
        joining_date: location.state.editEmp.joining_date,
      };
    } else {
      return {
        user_id: "",
        emp_name: "",
      };
    }
  });
  console.log("empList::", editEmp);

  // new emp
  const [employeeData, setEmployeeData] = useState([
    {
      emp_name: "", //to validate first empty fiels
      dept_id: "",
      role_id: "",
      emp_email: "",
      phone_no: "",
      joining_date: "",
    },
  ]);

  // to avoid empty post
  const [addEmp, setAddEmp] = useState([]);

  const handleInputChange = (e, index) => {
    const { id, value } = e.target;
    if (pageType === "edit") {
      setEditEmp({
        ...editEmp,
        [id]: value,
      });
    } else {
      let data = [...employeeData];
      data[index][id] = value;
      // setEmployeeData(data);
      setAddEmp(data);
    }
  };

  // submit
  const handleNextButtonClick = () => {
    if (pageType === "edit") {
      if (editEmp.emp_name.length > 0) {
        // let body = {
        //   ...editEmp,
        // };
        const { dept_name, role_name, ...rest } = editEmp;
        let body = { ...rest };

        const token = Cookies.get("token");
        editEmpImp(body, token)
          .then((res) => {
            showToast(res);
            navigate("/employees");
          })
          .catch((error) => {
            console.log("error editing department:", error);
            showToast("An error occurred during Department edit");
          });
      } else {
        showToast("fields can't be empty");
      }
    } else {
      if (employeeData[0].emp_name.length <= 0) {
        showToast("Please enter atleast one employee");
      } else {
        let body = [
          // ...employeeData
          ...addEmp,
        ];

        const token = Cookies.get("token");

        newEmp(body, token)
          .then((res) => {
            showToast(res);
            navigate("/employees");
          })
          .catch((error) => {
            console.log("error during newDept post:", error);
            showToast("An error occurred during new Department creation");
          });
      }
    }
  };

  const addFields = () => {
    let object = {
      dept_name: "",
      dept_head: "",
    };
    setEmployeeData([...employeeData, object]);
  };

  // const removeFields = (index) => {
  //   let data = [...employeeData];
  //   data.splice(index, 1);
  //   setEmployeeData(data);
  // };

  const removeFields = (index) => {
    if (employeeData.length > 1) {
      let data = [...employeeData];
      data.splice(index, 1);
      setEmployeeData(data);
      setAddEmp(data); // to avoid mid empty field post
    } else {
      showToast("can't delete the only fiels");
    }
  };

  const [activeItemId, setActiveItemId] = useState(1);
  const fileInputRef = useRef();
  const [documentFiles, setDocumentFiles] = useState([]);

  const handleFileChange = (e) => {
    const files = Array.from(e.target.files).map((file) => file.name);
    setDocumentFiles((prevFiles) => [...prevFiles, ...files]);
    console.log(documentFiles);
  };

  const handleRemoveFile = (index) => {
    setDocumentFiles((prevFiles) => {
      const updatedFiles = [...prevFiles];
      updatedFiles.splice(index, 1);
      return updatedFiles;
    });
  };

  const handleUploadButtonClick = () => {
    fileInputRef.current.click();
  };

  const handleFileUpload = () => {
    console.log("saved");
  };

  const handleBodyNavigationClick = (itemId) => {
    setActiveItemId(itemId);
  };

  const bodyNavigationItems = [
    { id: 1, name: "Single Add" },
    { id: 2, name: "Bulk Add" },
  ];

  return (
    <div className="createEditEmployees">
      <TopHeader />
      <div className="dashboardBody">
        <SidebarNav menuItems={SidebarMenuItems} />
        <div className="dashboardContent">
          <div className="headerBody">
            <h2>{pageType === "edit" ? "Edit" : "Create"} Employees</h2>
            <div className="actionHeader">
              <Link to={"/employees"} className="tableBtn">
                {" "}
                Back
              </Link>
            </div>
          </div>
          <div className="createEditEmployeesContainer">
            <div className="bodyNavigation">
              {bodyNavigationItems.map((item) => (
                <p
                  key={item.id}
                  className={`title ${
                    activeItemId === item.id ? "active" : ""
                  } ${pageType === "edit" && item.id === 2 ? "disabled" : ""}`}
                  onClick={() => handleBodyNavigationClick(item.id)}
                >
                  {item.name}
                </p>
              ))}
            </div>

            {activeItemId === 1 && (
              <>
                {employeeData.map((form, index) => {
                  return (
                    <div className="addSingle">
                      <div className="formFields">
                        <div className="formInput">
                          <label htmlFor="name">Employee Name</label>
                          <div className="inputField">
                            <input
                              id="emp_name"
                              // value={employeeData[index].emp_name}
                              value={
                                pageType === "edit"
                                  ? editEmp.emp_name
                                  : employeeData[index].emp_name
                              }
                              onChange={(e) => handleInputChange(e, index)}
                              placeholder="Enter employee name"
                            />
                          </div>
                        </div>
                        <div className="formInput">
                          <label htmlFor="department">Department</label>
                          <div className="inputField">
                            <select
                              id={pageType === "edit" ? "dept_id" : "dept_id"}
                              onChange={(e) => handleInputChange(e, index)}
                            >
                              <option value="">
                                {pageType === "edit"
                                  ? editEmp.dept_name
                                  : "Select department"}
                              </option>
                              {deptOptions.map((dept) => (
                                <option key={dept.dept_id} value={dept.dept_id}>
                                  {dept.dept_name}
                                </option>
                              ))}
                            </select>
                          </div>
                        </div>
                        <div className="formInput">
                          <label htmlFor="role">Role</label>
                          <div className="inputField">
                            <select
                              // id="role_id"
                              id={pageType === "edit" ? "role_id" : "role_id"}
                              onChange={(e) => handleInputChange(e, index)}
                            >
                              <option value="">
                                {pageType === "edit"
                                  ? editEmp.role_name
                                  : "Select role"}
                              </option>
                              {roleOptions.map((role) => (
                                <option key={role.role_id} value={role.role_id}>
                                  {role.role_name}
                                </option>
                              ))}
                            </select>
                          </div>
                        </div>
                        <div className="formInput">
                          <label htmlFor="email">Email</label>
                          <div className="inputField">
                            <input
                              type="email"
                              id="emp_email"
                              onChange={(e) => handleInputChange(e, index)}
                              // value={employeeData[index].emp_email}
                              value={
                                pageType === "edit"
                                  ? editEmp.emp_email
                                  : employeeData[index].emp_email
                              }
                              placeholder="Enter email"
                            />
                          </div>
                        </div>
                        <div className="formInput">
                          <label htmlFor="phone">Phone</label>
                          <div className="inputField">
                            <input
                              type="tel"
                              id="phone_no"
                              onChange={(e) => handleInputChange(e, index)}
                              // value={employeeData[index].phone_no}
                              value={
                                pageType === "edit"
                                  ? editEmp.phone_no
                                  : employeeData[index].phone_no
                              }
                              placeholder="Enter phone"
                            />
                          </div>
                        </div>
                        <div className="formInput">
                          <label htmlFor="joiningDate">Joining Date</label>
                          <div className="inputField">
                            <input
                              type="date"
                              id="joining_date"
                              onChange={(e) => handleInputChange(e, index)}
                              // value={employeeData[index].joining_date}
                              value={
                                pageType === "edit"
                                  ? editEmp.joining_date.split("T")[0]
                                  : employeeData[index].joining_date
                              }
                              placeholder="Select joining date"
                            />
                          </div>
                        </div>
                      </div>
                      {pageType === "create" && (
                        <div className="formActions">
                          <p className="add" onClick={addFields}>
                            <img src={ImageList.AddBlueIcon} alt="" /> Add new
                            department
                          </p>
                          <p
                            className="delete"
                            onClick={() => removeFields(index)}
                          >
                            <img src={ImageList.DeleteRedIcon} alt="" /> Delete
                          </p>
                        </div>
                      )}
                    </div>
                  );
                })}
              </>
            )}

            {activeItemId === 2 && pageType === "create" && (
              <div className="bulkUpload">
                <div className="first row">
                  <p className="bullet">1:</p>
                  <p className="firstTitle">
                    Download Template Employee List
                  </p>
                  <div className="downloadBtn">
                    {/* // eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                    <a href="#">
                      <img src={ImageList.DownloadIcon} alt="" />
                      Download
                    </a>
                  </div>
                </div>

                <div className="second row">
                  <p className="bullet">2:</p>
                  <div className="content">
                    <h6>Add or Edit the data in the template</h6>
                  </div>
                </div>

                <div className="third row">
                  <p className="bullet">3:</p>
                  <div
                    className="employeeDetails"
                    style={{ overflowX: "auto" }}
                  >
                    <table className="employeeTable">
                      <thead>
                        <tr>
                          <th>Employee Name</th>
                          <th>Department</th>
                          <th>Role</th>
                          <th>Employee Type</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td>Employee Name</td>
                          <td>Department</td>
                          <td>Role</td>
                          <td>Employee Type</td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </div>

                <div className="fourth row">
                  <p className="bullet">4:</p>
                  <div className="uploadContent">
                    <p className="title">Upload</p>
                    <div className="uploadArea">
                      <p className="titleHomework">Upload Employee List *</p>
                      <div className="uploadContainer">
                        <input
                          type="file"
                          name=""
                          id="fileInput"
                          ref={fileInputRef}
                          onChange={handleFileChange}
                          style={{ display: "none" }}
                        />
                        <button
                          className="uploadHomeworkBtn"
                          onClick={handleUploadButtonClick}
                        >
                          <img src={ImageList.WhiteUploadIcon} alt="" />
                          Upload
                        </button>
                        <div className="fileNames">
                          {documentFiles.map((fileName, index) => (
                            <p key={index} className="fileName">
                              {fileName}
                              <span
                                className="removeBtn"
                                onClick={() => handleRemoveFile(index)}
                              >
                                x Remove
                              </span>
                            </p>
                          ))}
                        </div>
                        {documentFiles.length === 0 && (
                          <div>
                            <p className="uploadInfo">No file chosen</p>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            )}

            <div>
              <button onClick={handleNextButtonClick} className="nextBtn">
                {pageType === "edit" ? "Update" : "save"}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CreateEditEmployees;
