import React, { useEffect, useState } from "react";
import TopHeader from "../../components/TopHeader/TopHeader";
import SidebarNav from "../../components/SideBar/SideBar";
import SidebarMenuItems from "../../utils/SidebarMenuItems";
import "./CreateEditDepartment.scss";
import ImageList from "../../utils/ImageList";
import { Link, useNavigate, useParams, useLocation } from "react-router-dom";
import { showToasterMessage } from "../../utils/helper";
import Cookies from "js-cookie";
import newDept from "../../interface/implementation/newDeptImp";
import editDeptImp from "../../interface/implementation/editDeptImp";

function CreateEditDepartment() {
  const { type } = useParams();

  const navigate = useNavigate();

  const [pageType, setPageType] = useState("");

  useEffect(() => {
    setPageType(type);
  }, [type]);

  const showToast = (errorText) => {
    showToasterMessage(errorText);
  };

  const location = useLocation();

  const [editDept, setEditDept] = useState(() => {
    if (type === "edit") {
      return {
        dept_id: location.state.editDept.dept_id,
        dept_name: location.state.editDept.dept_name,
        dept_head: location.state.editDept.dept_head,
      };
    }
  });

  // new department
  const [departmentInfo, setDepartmentInfo] = useState([
    {
      dept_name: "", //to validate empty field (first)
    },
  ]);

  // to avoid empty post
  const [addDept, setAddDept] = useState([]);

  const handleInputChange = (e, index) => {
    const { id, value } = e.target;
    if (pageType == "edit") {
      setEditDept({
        ...editDept,
        [id]: value,
      });
    } else {
      let data = [...departmentInfo];
      data[index][e.target.id] = e.target.value;
      // setDepartmentInfo(data);
      setAddDept(data);
    }
  };

  //   submit
  const handleNextButtonClick = () => {
    if (pageType === "edit") {
      if (editDept.dept_name.length > 0) {
        let body = {
          ...editDept,
        };
        const token = Cookies.get("token");
        editDeptImp(body, token)
          .then((res) => {
            showToast(res);
            navigate("/departments");
          })
          .catch((error) => {
            console.log("error editing department:", error);
            showToast("An error occurred during Department edit");
          });
      } else {
        showToast("fields can't be empty");
      }
    } else {
      if (departmentInfo[0].dept_name.length <= 0) {
        showToast("Please enter atleast one Department");
      } else {
        let body = [
          // ...departmentInfo
          ...addDept,
        ];

        const token = Cookies.get("token");

        newDept(body, token)
          .then((res) => {
            showToast(res);
            navigate("/departments");
          })
          .catch((error) => {
            console.log("error during newDept post:", error);
            showToast("An error occurred during new Department creation");
          });
      }
    }
  };

  const addFields = () => {
    let object = {
      dept_name: "",
      dept_head: "",
    };
    setDepartmentInfo([...departmentInfo, object]);
  };

  const removeFields = (index) => {
    if (departmentInfo.length > 1) {
      let data = [...departmentInfo];
      data.splice(index, 1);
      setDepartmentInfo(data);
      setAddDept(data); // to avoid mid empty field post
    } else {
      showToast("can't delete the only fiels");
    }
  };

  return (
    <div className="createEditDepartment">
      <TopHeader />
      <div className="dashboardBody">
        <SidebarNav menuItems={SidebarMenuItems} />
        <div className="dashboardContent">
          <div className="headerBody">
            <h2>{pageType === "edit" ? "Edit" : "Create"} Departments</h2>
            <div className="actionHeader">
              <Link to={"/departments"} className="tableBtn">
                {" "}
                Back
              </Link>
            </div>
          </div>
          <div className="createDepartmentContainer">
            {departmentInfo.map((form, index) => {
              return (
                <div className="departmentContent" key={index}>
                  <div className="formFields">
                    <div className="formInput">
                      <label htmlFor="departmentName">
                        Name of the Department *{" "}
                      </label>
                      <div className="inputField">
                        <input
                          id="dept_name"
                          value={
                            pageType === "edit"
                              ? editDept.dept_name
                              : departmentInfo[index].dept_name
                          }
                          onChange={(e) => handleInputChange(e, index)}
                          placeholder="Enter department name"
                        />
                      </div>
                      <div className="inputField">
                        <input
                          id="dept_head"
                          value={
                            pageType === "edit"
                              ? editDept.dept_head
                              : departmentInfo[index].dept_head
                          }
                          onChange={(e) => handleInputChange(e, index)}
                          placeholder="Enter department head"
                        />
                      </div>
                    </div>
                  </div>

                  {pageType === "create" && (
                    <div className="formActions">
                      <p className="add" onClick={addFields}>
                        <img src={ImageList.AddBlueIcon} alt="" /> Add new
                        department
                      </p>
                      <p className="delete" onClick={() => removeFields(index)}>
                        <img src={ImageList.DeleteRedIcon} alt="" /> Delete
                      </p>
                    </div>
                  )}
                </div>
              );
            })}

            <button onClick={handleNextButtonClick} className="nextBtn">
              {pageType === "edit" ? "Update" : "Save"}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CreateEditDepartment;
