import autoCompleteNameAPI from "../api/autoCompleteNameAPI";

export default function autoCompleteName(body, token) {
  //console.log(accessToken);
  return autoCompleteNameAPI
      .post(body, token)
      .then((res) => {
        console.log("Response ", res);
        return res; 
    })
    .catch((err) => {
        console.log("Error", err);
        throw err; 
    });
  
}